import { IDatos } from './clima-info/interfaces/datos.interface';
import { IClima } from './clima-info/interfaces/clima.interface';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AppService {

  private _mostrar:boolean=true;
  private mostrar$=new Subject<boolean>();
  private _baseUrl='https://www.metaweather.com/api';
  private _datos!:IDatos;
  private datos$=new Subject<IDatos>();

  toogleMostrar(toogle:boolean=this._mostrar){
    if(!this._datos)return;
    this._mostrar=!toogle;
    this.mostrar$.next(this._mostrar);
  }

  get getMostrar$():Observable<boolean>{
    return this.mostrar$.asObservable();
  }

  asignarDatos(datos:IDatos){
    this._datos=datos;
    this.datos$.next(this._datos);
    this.toogleMostrar(true);
  }

  get getDatos():Observable<IDatos>{
    return this.datos$.asObservable();
  }

  constructor(private http:HttpClient) { }

  buscar(q:string):Observable<IClima[]>{
    return this.http.get<IClima[]>(`${this._baseUrl}/location/search/?query=${q}`);
  }

  cargarDatos(q:number):Observable<any>{
    return this.http.get<any>(`${this._baseUrl}/location/${q}`);
  }
}
