import { IDatos } from './../interfaces/datos.interface';
import { AppService } from './../../app.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-barra-info',
  templateUrl: './barra-info.component.html',
  styles: [
  ]
})
export class BarraInfoComponent implements OnInit {

  datos!:IDatos;
  cargarPantalla:boolean=false;


  constructor(private appService:AppService) { }

  mostrarSidebar(){
    this.appService.toogleMostrar();
  }

  cargarImagen(url:string,img:string){
    return `${url}${this.datos.consolidated_weather[0].weather_state_name.replace(/ /g, "")}.png`;
  }

  ngOnInit(): void {
    this.appService.getDatos
      .subscribe(datos=>{
        this.cargarPantalla=true;
        this.datos=datos
      })
  }

}
