import { AppService } from './../../app.service';
import { IDatos } from './../interfaces/datos.interface';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styles: [
  ]
})
export class MainComponent implements OnInit {

  datos:any=[];
  datosHoy:any;
  cargarPantalla:boolean=false;

  constructor(private appService:AppService){

  }

  ngOnInit(): void {
    this.appService.getDatos
      .subscribe(datos=>{
        this.cargarPantalla=true;
        this.datos={...datos};
        this.datos.consolidated_weather=this.datos.consolidated_weather.splice(1,5);
        this.datosHoy=datos.consolidated_weather[0];
      });

  }

}
